const handle = (method) => async ({ request, fetch, url }) => {
  const headers = new Headers();
  headers.append("content-type", request.headers.get("content-type"));
  headers.append("accept", request.headers.get("accept"));
  return fetch(new URL(url.pathname + "?", new URL("https://conprogramming--sveltekit-app.modal.run/")) + url.search, { headers, method, body: request.body, signal: request.signal, duplex: "half" });
};
const GET = handle("GET");
const POST = handle("POST");
const PATCH = handle("PATCH");
const PUT = handle("PUT");
const DELETE = handle("DELETE");
export {
  DELETE,
  GET,
  PATCH,
  POST,
  PUT
};
